body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.item {
  padding: 25px;
  border: 2px solid black;
}




/* CONTACT CSS */
.card {
  background-color: cadetblue;
  color: aliceblue;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 25px;
  height: auto;
  width: 50%;
  padding: 25px;
  border: none;
  border-radius: 5px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "div1 div2";
}
.img{
  grid-area: div1;
  display: flex !important;
  justify-content: center !important;
  text-align: center !important;
}

.info{
  grid-area: div2;
}
img{
  height: 150px;
  width: 150px;
  border-radius: 5px;
}

.card > div > button {
  cursor: pointer;
  color:aliceblue;
  width: 100px;
  height: 40px;
  border: 2px solid rgb(255, 255, 255);
  background-color: cadetblue;
  border-radius: 5px;
}
.card > div > button:hover {
  color:rgb(0, 0, 0);
  border: 2px solid rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
  transition: 0.5s;
}
@media screen and (max-width:550px) {
  .card{
    width: 80%;
    margin: auto;
    margin-top: 25px;
    margin-bottom: 25px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "div1"
    "div2";
  }
  img{
    width: 90%;
    height: 90%;
    border-radius: 5px;
  }
}

/* CINEMA_SEATING */
.theatre
{
  width: 520px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.seats{
  background-color:rgb(255, 255, 255);
  height: 22px;
  width: 40px;
  border: 1px solid rgb(211, 210, 210);
  border-radius: 3px;
  margin: 5px;
  text-align: center;
  cursor: pointer;
}
.seats:hover{
  background-color: rgb(145, 145, 145);
}

h1,h2,h3{
  text-align: center;
  color: rgb(53, 221, 67);
}

.seating_arrangement{
  width: 100%;
  margin: auto;
  height: 15vh;
  background-color: rgb(255, 255, 255);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  column-gap: 5vh;
  grid-template-areas:
  "div1 div1";
}

.gold_seat{
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  grid-area: div1;
}

.seating_arrangement2{
  width: 100%;
  background-color: rgb(255, 255, 255);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  column-gap: 10vh;
  grid-template-areas:
  "div2 div3"
  "div2 div3";
}

.silver_seat1{
  display: flex;
  flex-wrap: wrap;
  grid-area: div2;
  margin: 0;
  padding: 0;
}
.silver_seat2{
  display: flex;
  flex-wrap: wrap;
  grid-area: div3;
  margin: 0;
  padding: 0;
}

.screen{
  border-radius: 5px;
  margin: auto;
  width: 80%;
  height: 50px;
  background-color: rgb(230, 230, 230);
  text-align: center;
  color: rgb(53, 221, 67);
}

#gs{
  color: rgb(45, 248, 255);
}
#ss{
  color: rgb(98, 177, 19);
}

@media screen and (max-width:520px) {
  .theatre
{
  width: auto;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

  .seats{
    height: 15px;
    width: 20px;
    font-size:xx-small;
  }

  .seating_arrangement{
    width: 90%;
    margin: auto;
    height: auto;
    background-color: rgb(255, 255, 255);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    column-gap: 5vh;
    grid-template-areas:
    "div1 div1";
  }

  .seating_arrangement2{
    width: 90%;
    margin: auto;
    background-color: rgb(255, 255, 255);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    column-gap: 1vh;
    grid-template-areas:
    "div2 div3"
    "div2 div3";
  }
}